import React, { useEffect, useMemo, useState } from "react";
import { useHistory } from "react-router-dom";
import axios from "axios";
import { api } from "../api.js";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
const data = [
  {
    name: "ข้าว",
    num: "rice",
  },
  {
    name: "อ้อย",
    num: "sugarcane",
  },
  {
    name: "มันสำปะหลัง",
    num: "cassava",
  },
  {
    name: "ปาล์ม",
    num: "plam",
  },
  {
    name: "ข้าวโพด",
    num: "sweetcorn",
  },
];
function Home() {
  let history = useHistory();
  const [datass, setdatass] = useState([]);

  useEffect(() => {
    const interval = setInterval(() => {
      axios.get(api + "plantname").then((response) => {
        setdatass(response.data);
      });
    }, 2000);
    return () => clearInterval(interval);
  }, []);

  const teatapi = "200-326-333";
  
  var positionN = teatapi.indexOf("-");
  var PreP = teatapi.substring(teatapi.indexOf("-")+1);
  var positionP = PreP.indexOf("-");
  // var positionK = PreP.indexOf("-");
  var ResultN = teatapi.substring(0,teatapi.indexOf("-"));
  var ResultP = PreP.substring(0,PreP.indexOf("-"));
  var ResultK = PreP.substring(positionP+1);

  console.log("plantname = " + ResultK);
  console.log("plantname = "+ PreP);

  return (
    <div className="App bg-light">
      <div class="row row-cols-3">
        {data.map((index, key) => {
          return (
            <div class="col box-type">
              <div class="d-grid gap-2 col-12 mx-auto" key={key}>
                <button
                  class="btn btn-primary btn-index"
                  type="button"
                  onClick={() => {
                    history.push("/" + index.num);
                  }}
                >
                  {index.name}
                </button>
              </div>
            </div>
          );
        })}
        {datass.map((index, key) => {
          return (
            <div class="col box-type">
              <div class="d-grid gap-2 col-12 mx-auto" key={key}>
                <button
                  class="btn btn-primary btn-index"
                  type="button"
                  onClick={() => {
                    history.push({
                      pathname: "/sent-now",
                      search: "?query=" + index.plant_name,
                    });
                  }}
                >
                  {index.plant_name}
                </button>
              </div>
            </div>
          );
        })}
        <div class="col box-type">
          <div class="d-grid gap-2 col-12 mx-auto">
            <button
              class="btn btn-success btn-index"
              type="button"
              onClick={() => {
                history.push("/insert-item");
              }}
            >
              เพิ่มชนิดของพืช
            </button>
          </div>
        </div>
      </div>
      <div className="row btn-delete">
        <div className="col-10"></div>
        <div className="col">
          <button
            type="button"
            className="btn btn-danger form-control btn-index"
            onClick={() => {
              history.push("/delete-plant");
            }}
          >
            <i class="bi bi-trash"></i>
            &nbsp; ลบ
          </button>
        </div>
      </div>
    </div>
  );
}

export default Home;
